@import "../components/../inc/vars";
@import "../components/../inc/media";

.info-user {
  display: flex;
  align-items: center;
  gap: 24px;

  &__wrap {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 50px 0;
    margin-bottom: 50px;
  }

  &__description {
    padding: 18px;
    border-radius: 10px;
    background-color: $bg-white;
  }

  &__name {
    font-size: 18px;
    font-weight: 600;
  }

  &__text {
    color: $grey-01;
    margin-top: 10px;
  }

  &__media {
    display: flex;
    align-items: center;
    gap: 16px;
    margin-top: 12px;
  }

  &__link {
    display: block;
    width: 24px;
    height: 24px;
    color: $grey-01;

    &--fb {
      color: #0866ff;
    }

    &--in {
      color: #0a66c2;
    }
  }
}

.position-right {
  margin: 25px 0 0 30px;
}

.position-left {
  width: max-content;
  margin: 25px 0 0 auto;
}
