@import "../components/../inc/vars";
@import "../components/../inc/media";

.header {
  position: relative;
  margin-bottom: 160px;
  z-index: 2;

  &__top {
    position: sticky;
    width: 100%;
    top: 0;
    left: 0;
    background-color: $bg-white;
    box-shadow: $header-shadow;
  }

  &__container {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 24px 0;
  }

  &__title {
    width: 100%;
    max-width: 500px;
    font-size: 56px;
    font-weight: 600;
    letter-spacing: 2px;
    background-color: $bg-white;
    box-shadow: $bg-shadow;
    padding: 24px;
    margin: 120px 0 0;

    @include tablet {
      font-size: 41px;
      margin: 90px 0 0;
    }
  }

  &__text {
    width: 100%;
    max-width: 650px;
    font-size: 32px;
    background-color: $bg-white;
    box-shadow: $bg-shadow-reverse;
    padding: 24px;
    margin: 32px 0 0 auto;
  }
}
