@import "../components/../inc/vars";

.nav {
  display: flex;
  align-items: center;
  gap: 18px;

  &__item {
    display: block;
    position: relative;
    font-size: 18px;
    letter-spacing: 0.5px;
    font-weight: 600;
    color: $black;
    text-decoration: none;

    &:before {
      content: '';
      position: absolute;
      left: 0;
      bottom: -2px;
      width: 0;
      height: 2px;
      background-color: $black;
      transition: width 0.3s ease, left 0.3s ease;
    }

    &:hover:before {
      width: 100%;
      left: 0;
    }

    &:before {
      width: 0;
      left: auto;
      right: 0;
    }

    &:hover:before {
      left: 0;
      right: auto;
      width: 100%;
    }

  }
}
