@import "vars";
@import "media";

*,
*:before,
*:after {
  box-sizing: border-box;
}

body {
  font-family: "Roboto", sans-serif;
  font-size: 16px;
  font-weight: 400;
  line-height: 1.4;
  font-style: normal;
  color: $black;
  margin: 0;
  background-color: $bg-white;
  min-width: 375px;
}

video {
  border: 0;
  outline: none;
}

img {
  outline: none;
  max-width: 100%;
}

._bold {
  font-weight: 700;
}
