@import "../components/../inc/vars";
@import "../components/../inc/media";

.our-products {
  &__container {
    display: flex;
    align-items: flex-start;
    gap: 24px;
    background-color: $bg-white;
    padding: 24px;
  }

  &__img-box {
    width: 100%;
    max-width: 500px;
    height: 500px;

    img {
      display: block;
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }

  &__link {
    display: block;
    position: relative;
    font-weight: 600;
    letter-spacing: 5px;
    text-decoration: none;
    text-transform: uppercase;
    text-align: center;
    color: $black;
    background-color: lighten($black, 90%);
    margin-bottom: 24px;

    &:after, &:before {
      content: "";
      position: absolute;
      top: 0;
      width: 15%;
      height: 2px;
      background-color: rgb(54, 56, 55);
      transition: width 0.2s 0.35s ease-out;
    }

    &:before {
      right: 50%;
    }

    &:after {
      left: 50%;
    }

    &:hover {
      &:before, &:after {
        width: 50%;
        transition: width 0.2s ease-out;
      }
    }
  }

  &__link-text {
    display: block;
    position: relative;
    top: 0;
    left: 0;
    width: 100%;
    padding: 20px;
    transition: 0.3s;

    &:after, &:before {
      position: absolute;
      content: "";
      top: 0;
      width: 0;
      height: 0;
      opacity: 0;
      z-index: 2;
      transition: width 0.2s ease-in, height 0.15s 0.2s linear, opacity 0s 0.35s;
    }

    &:before {
      left: 0;
      opacity: 0;
      border-left: 2px solid rgb(54, 56, 55);
      border-bottom: 2px solid rgb(54, 56, 55);
    }

    &:after {
      right: 0;
      opacity: 0;
      border-right: 2px solid rgb(54, 56, 55);
      border-bottom: 2px solid rgb(54, 56, 55);
    }

    &:hover {
      &:before, &:after {
        width: 50%;
        height: 100%;
        opacity: 1;
        transition: height 0.2s 0.2s ease-in, width 0.2s 0.4s linear, opacity 0s 0.2s;
      }
    }
  }

  &__text {
    font-size: 24px;
  }
}
