@import "../inc/vars";

.page {
  display: flex;
  flex-direction: column;
  min-height: 100vh;

  &__main {
    flex: 1;
    max-width: 100%;
    overflow: hidden;
    padding-bottom: 20px;
  }
}

.overlay {
  position: relative;
  background-image: url("../img/musdev-bg-min.webp");
  background-size: cover;
  background-position: center;
  background-attachment: fixed;
  z-index: 1;

  &:after {
    content: "";
    position: absolute;
    inset: 0;
    background-color: rgba(0, 0, 0, 0.7);
  }
}

.container {
  width: 100%;
  max-width: 1920px;
  margin: 0 auto;
  padding: 0 32px;
}

.common-title {
  font-size: 45px;
  font-weight: 900;
  color: $white;
  margin-bottom: 50px;
}
