$desktop: 1025px;
$tablet: 1024px;
$tabletMin: 768px;
$mobile: 767px;

@mixin max-tablet {
  @media (max-width: 1279px) {
    @content;
  }
}

@mixin tablet {
  @media (max-width: $tablet) {
    @content;
  }
}

@mixin max-mobile {
  @media (max-width: 767px) {
    @content;
  }
}

@mixin min-tablet {
  @media (min-width: 768px) {
    @content;
  }
}

.tablet-hidden {
  @include max-tablet {
    display: none !important;
  }
}
