@import "../components/../inc/vars";
@import "../components/../inc/media";

.video-box {
  width: 100%;
  max-width: 1024px;
  margin: 0 auto;

  &__title {
    font-size: 32px;
    font-weight: 600;
    color: $white;
    margin-bottom: 30px;
  }

  &__wrap {
    display: flex;
    align-items: stretch;
    justify-content: space-between;
    gap: 24px;
    margin-bottom: 24px;
  }

  &__frame {
    width: 50%;
  }

  &__link {
    font-size: 24px;
    text-align: center;
    font-weight: 900;
    color: $grey-01;
    transition: opacity 0.3s;

    &:hover {
      opacity: 0.5;
    }
  }
}
